'use client';

import React, { useState, useEffect } from 'react';
import { Home, BarChart3, TrendingUp, Settings, Activity, FileText, Briefcase, Code, MessageSquare, Monitor, Bell, RefreshCw } from 'lucide-react';

interface StatCard {
  title: string;
  value: string;
  change: string;
  trend: 'up' | 'down';
  icon: React.ReactNode;
}

interface NotificationItem {
  id: string;
  type: 'info' | 'warning' | 'success';
  message: string;
  timestamp: Date;
}

export default function HomePage() {
  const [activeTab, setActiveTab] = useState('home');
  const [notifications, setNotifications] = useState<NotificationItem[]>([
    { id: '1', type: 'info', message: 'Memory monitoring active', timestamp: new Date() },
    { id: '2', type: 'success', message: 'Database connection established', timestamp: new Date() }
  ]);
  const [isRefreshing, setIsRefreshing] = useState(false);

  // 실시간 데이터 시뮬레이션
  const [realTimeStats, setRealTimeStats] = useState({
    cpuUsage: 45,
    memoryUsage: 62,
    activeConnections: 12,
    dataTransfer: '1.2MB/s'
  });

  useEffect(() => {
    const interval = setInterval(() => {
      setRealTimeStats(prev => ({
        cpuUsage: Math.max(10, Math.min(90, prev.cpuUsage + (Math.random() - 0.5) * 10)),
        memoryUsage: Math.max(20, Math.min(95, prev.memoryUsage + (Math.random() - 0.5) * 5)),
        activeConnections: Math.max(0, Math.min(50, prev.activeConnections + Math.floor((Math.random() - 0.5) * 3))),
        dataTransfer: `${(Math.random() * 5 + 0.5).toFixed(1)}MB/s`
      }));
    }, 2000);

    return () => clearInterval(interval);
  }, []);

  const handleRefresh = async () => {
    setIsRefreshing(true);
    // 실제 데이터 새로고침 로직
    await new Promise(resolve => setTimeout(resolve, 1000));
    setIsRefreshing(false);
    
    // 새로운 알림 추가
    const newNotification: NotificationItem = {
      id: Date.now().toString(),
      type: 'success',
      message: 'Data refreshed successfully',
      timestamp: new Date()
    };
    setNotifications(prev => [newNotification, ...prev.slice(0, 4)]);
  };

  const statCards: StatCard[] = [
    {
      title: '총 타이핑 시간',
      value: '47.2h',
      change: '+12%',
      trend: 'up',
      icon: <Activity className="w-5 h-5" />
    },
    {
      title: '문서 작업',
      value: '142',
      change: '+8%',
      trend: 'up',
      icon: <FileText className="w-5 h-5" />
    },
    {
      title: '코딩 세션',
      value: '89',
      change: '-2%',
      trend: 'down',
      icon: <Code className="w-5 h-5" />
    },
    {
      title: '메모리 사용률',
      value: `${realTimeStats.memoryUsage}%`,
      change: '+5%',
      trend: 'up',
      icon: <Monitor className="w-5 h-5" />
    }
  ];

  const navigationItems = [
    { id: 'home', label: '홈', icon: <Home className="w-4 h-4" /> },
    { id: 'stats', label: '통계', icon: <BarChart3 className="w-4 h-4" /> },
    { id: 'analysis', label: '분석', icon: <TrendingUp className="w-4 h-4" /> },
    { id: 'settings', label: '설정', icon: <Settings className="w-4 h-4" /> }
  ];

  const sectionItems = [
    { id: 'typing', label: '타이핑 분석', icon: <Activity className="w-5 h-5" />, color: 'bg-blue-500' },
    { id: 'docs', label: 'Documents', icon: <FileText className="w-5 h-5" />, color: 'bg-green-500' },
    { id: 'office', label: 'Office 작업', icon: <Briefcase className="w-5 h-5" />, color: 'bg-orange-500' },
    { id: 'coding', label: 'Coding', icon: <Code className="w-5 h-5" />, color: 'bg-purple-500' },
    { id: 'sns', label: 'SNS 활동', icon: <MessageSquare className="w-5 h-5" />, color: 'bg-pink-500' },
    { id: 'monitor', label: '실시간 모니터', icon: <Monitor className="w-5 h-5" />, color: 'bg-cyan-500' }
  ];

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      {/* 네비게이션 헤더 */}
      <header className="bg-white dark:bg-gray-800 shadow-sm border-b border-gray-200 dark:border-gray-700">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* 로고 */}
            <div className="flex items-center">
              <h1 className="text-xl font-bold text-gray-900 dark:text-white">Loop 6</h1>
            </div>

            {/* 네비게이션 버튼들 - 간격 넓게, outline 제거 */}
            <nav className="flex items-center space-x-8">
              {navigationItems.map((item) => (
                <button
                  key={item.id}
                  onClick={() => setActiveTab(item.id)}
                  className={`
                    flex items-center space-x-2 px-4 py-2 rounded-lg transition-all duration-200
                    focus:outline-none focus:ring-0 hover:outline-none
                    ${activeTab === item.id
                      ? 'bg-gray-900 dark:bg-gray-100 text-white dark:text-gray-900'
                      : 'text-gray-600 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-gray-900 dark:hover:text-gray-100'
                    }
                  `}
                >
                  {item.icon}
                  <span className="font-medium">{item.label}</span>
                </button>
              ))}
            </nav>

            {/* 알림 및 새로고침 버튼 */}
            <div className="flex items-center space-x-4">
              <button
                onClick={handleRefresh}
                disabled={isRefreshing}
                className="
                  p-2 rounded-lg bg-gray-100 dark:bg-gray-700 text-gray-600 dark:text-gray-400
                  hover:bg-gray-200 dark:hover:bg-gray-600 hover:text-gray-900 dark:hover:text-gray-100
                  focus:outline-none focus:ring-0 hover:outline-none
                  transition-all duration-200 disabled:opacity-50
                "
              >
                <RefreshCw className={`w-4 h-4 ${isRefreshing ? 'animate-spin' : ''}`} />
              </button>
              
              <div className="relative">
                <button className="
                  p-2 rounded-lg bg-gray-100 dark:bg-gray-700 text-gray-600 dark:text-gray-400
                  hover:bg-gray-200 dark:hover:bg-gray-600 hover:text-gray-900 dark:hover:text-gray-100
                  focus:outline-none focus:ring-0 hover:outline-none
                  transition-all duration-200
                ">
                  <Bell className="w-4 h-4" />
                  {notifications.length > 0 && (
                    <span className="absolute -top-1 -right-1 w-3 h-3 bg-red-500 rounded-full text-xs"></span>
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* 메인 컨텐츠 */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* 통계 카드 그리드 */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          {statCards.map((card, index) => (
            <div
              key={index}
              className="bg-white dark:bg-gray-800 rounded-xl p-6 shadow-sm border border-gray-200 dark:border-gray-700 hover:shadow-md transition-shadow duration-200"
            >
              <div className="flex items-center justify-between mb-4">
                <div className="p-2 rounded-lg bg-gray-100 dark:bg-gray-700 text-gray-600 dark:text-gray-400">
                  {card.icon}
                </div>
                <span className={`text-sm font-medium ${
                  card.trend === 'up' ? 'text-green-600 dark:text-green-400' : 'text-red-600 dark:text-red-400'
                }`}>
                  {card.change}
                </span>
              </div>
              <h3 className="text-sm font-medium text-gray-600 dark:text-gray-400 mb-1">{card.title}</h3>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">{card.value}</p>
            </div>
          ))}
        </div>

        {/* 섹션 그리드 */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
          {sectionItems.map((section) => (
            <button
              key={section.id}
              className="
                bg-white dark:bg-gray-800 rounded-xl p-6 shadow-sm border border-gray-200 dark:border-gray-700
                hover:shadow-md hover:scale-105 transition-all duration-200
                focus:outline-none focus:ring-0 hover:outline-none
                text-left
              "
            >
              <div className="flex items-center space-x-4 mb-4">
                <div className={`p-3 rounded-lg ${section.color} text-white`}>
                  {section.icon}
                </div>
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">{section.label}</h3>
              </div>
              <p className="text-gray-600 dark:text-gray-400 text-sm">
                {section.id === 'typing' && '타이핑 패턴과 속도를 분석합니다'}
                {section.id === 'docs' && '문서 작업 현황을 확인합니다'}
                {section.id === 'office' && 'Office 애플리케이션 사용량을 추적합니다'}
                {section.id === 'coding' && '코딩 활동과 프로젝트를 관리합니다'}
                {section.id === 'sns' && 'SNS 및 커뮤니케이션 활동을 모니터링합니다'}
                {section.id === 'monitor' && '시스템 리소스를 실시간으로 모니터링합니다'}
              </p>
            </button>
          ))}
        </div>

        {/* 실시간 통계 대시보드 */}
        <div className="bg-white dark:bg-gray-800 rounded-xl p-6 shadow-sm border border-gray-200 dark:border-gray-700">
          <h2 className="text-xl font-bold text-gray-900 dark:text-white mb-6">실시간 시스템 모니터</h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-600 dark:text-gray-400">CPU 사용률</span>
                <span className="text-sm font-bold text-gray-900 dark:text-white">{realTimeStats.cpuUsage.toFixed(1)}%</span>
              </div>
              <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                <div 
                  className="bg-blue-500 h-2 rounded-full transition-all duration-500"
                  style={{ width: `${realTimeStats.cpuUsage}%` }}
                />
              </div>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-600 dark:text-gray-400">메모리 사용률</span>
                <span className="text-sm font-bold text-gray-900 dark:text-white">{realTimeStats.memoryUsage.toFixed(1)}%</span>
              </div>
              <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                <div 
                  className="bg-green-500 h-2 rounded-full transition-all duration-500"
                  style={{ width: `${realTimeStats.memoryUsage}%` }}
                />
              </div>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-600 dark:text-gray-400">활성 연결</span>
                <span className="text-sm font-bold text-gray-900 dark:text-white">{realTimeStats.activeConnections}</span>
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-500">개의 활성 연결</div>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-600 dark:text-gray-400">데이터 전송</span>
                <span className="text-sm font-bold text-gray-900 dark:text-white">{realTimeStats.dataTransfer}</span>
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-500">실시간 전송률</div>
            </div>
          </div>
        </div>

        {/* 알림 패널 */}
        {notifications.length > 0 && (
          <div className="mt-8 bg-white dark:bg-gray-800 rounded-xl p-6 shadow-sm border border-gray-200 dark:border-gray-700">
            <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">최근 알림</h2>
            <div className="space-y-3">
              {notifications.slice(0, 3).map((notification) => (
                <div
                  key={notification.id}
                  className={`
                    p-3 rounded-lg border-l-4 
                    ${notification.type === 'info' ? 'bg-blue-50 dark:bg-blue-900/20 border-blue-500' : ''}
                    ${notification.type === 'success' ? 'bg-green-50 dark:bg-green-900/20 border-green-500' : ''}
                    ${notification.type === 'warning' ? 'bg-yellow-50 dark:bg-yellow-900/20 border-yellow-500' : ''}
                  `}
                >
                  <div className="flex justify-between items-start">
                    <p className="text-sm text-gray-900 dark:text-white">{notification.message}</p>
                    <span className="text-xs text-gray-500 dark:text-gray-400">
                      {notification.timestamp.toLocaleTimeString()}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
